<template>
  <div class="modal-overlay">
    <div class="modal">
      <button class="close-button" @click="this.$emit('closeModal');">×</button>
      <h2>계산 결과</h2>

      <div v-for="(givePriceMap, giver, index) in calculateResult" :key="index">
        <h3 style="margin-bottom: 5px;">{{ giver }}</h3>
        <p v-for="(givePrice, taker) in givePriceMap" :key="taker" style="margin: 5px;">→ {{ taker }} : {{ givePrice }}원</p>
      </div>

      <div class="modal-actions">
        <button class="add-button" @click="copyResult()">복사</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CalculateResultModal",
  props: {
    people: {
      required: false
    },
    payments: {
      required: false
    },
  },
  mounted() {
    if(this.payments.length == 0){
      alert("결제내역을 추가해주세요.");
      this.$emit('closeModal');
    }

    this.calculate()
  },
  data() {
    return {
      calculateResult: {},
    }
  },
  methods: {
    calculate(){
      let moneyMap = {};
      for (let person of this.people){
        moneyMap[person] = {};
      }

      for(let payment of this.payments){
        let dividedPrice = Math.floor(payment.amount / payment.participants.length);

        for(let participant of payment.participants){
          if(participant == payment.payer){
            continue;
          }

          let tempPrice = moneyMap[participant][payment.payer] ?? 0;

          moneyMap[participant][payment.payer] = tempPrice + dividedPrice;
        }
      }

      for(let giver in moneyMap){
        for(let taker in moneyMap[giver]){
          let givePrice = moneyMap[giver][taker];
          let takePrice = moneyMap[taker][giver] ?? 0;

          if (takePrice > 0 && givePrice >= takePrice){
            moneyMap[giver][taker] = givePrice - takePrice;
            moneyMap[taker][giver] = 0;
          }
        }
      }

      this.calculateResult = {};
      for (let giver of this.people){
        let newGivePriceMap = {};

        for(let taker in moneyMap[giver]){
          let givePrice = moneyMap[giver][taker] ?? 0;

          if (givePrice > 0){
            newGivePriceMap[taker] = givePrice;
          }
        }

        if (Object.keys(newGivePriceMap).length > 0){
          this.calculateResult[giver] = newGivePriceMap;
        }
      }
    },
    async copyResult(){
      let copyText = '';

      for(let giver in this.calculateResult){
        let givePriceText = '';

        for(let taker in this.calculateResult[giver]){
          let givePrice = this.calculateResult[giver][taker] ?? 0;

          givePriceText += (' → ' + taker + ' : ' + givePrice + '원\n' );
        }

        copyText += (giver += '\n');
        copyText += givePriceText;
        copyText += '\n';
      }

      await navigator.clipboard.writeText(copyText);

      alert("복사 완료");
    },
  }
}
</script>