<template>
  <div class="payment">
    <div v-if="payments.length == 0">
      ❗결제내역이 없습니다.
    </div>
    <div class="payment-item" v-for="(payment, index) in payments" :key="index" @click="this.$emit('openEditPaymentModal', index)">
      <div class="payment-info">
        <div class="store">{{ payment.description }}</div>
        <div class="amount">{{ payment.amount.toLocaleString() }}원</div>
      </div>
      <div class="payer-participants">
        <div class="payer">{{ payment.payer }}</div>
        <span class="separator"> | </span>
        <div class="participants">{{ payment.participants.length == people.length ? '전체' : payment.participants.join(', ') }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PaymentList",
  props: {
    people: {
      required: false
    },
    payments: {
      required: false
    },
  }
}
</script>