<template>
  <div class="people">
    <div v-if="people.length == 0">
      ❗사용자가 없습니다.
    </div>
    <div class="person-item" v-for="person in people" :key="person">
      {{ person }} <span class="user-delete-button" @click="deletePerson(person)">✖</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "PeopleList",
  props: {
    people: {
      required: false
    },
    payments: {
      required: false
    },
  },
  methods: {
    deletePerson(person){
      if (!confirm("해당 사용자가 결제자이거나 유일한 참가자인 결제내역이 모두 삭제됩니다. \n삭제하시겠습니까?")) {
        return;
      }

      let people = this.people;
      let payments = this.payments;
      payments = payments.filter(payment => payment.payer !== person);
      payments.forEach(payment => { payment.participants = payment.participants.filter(participant => participant !== person) })
      payments = payments.filter(payment => payment.participants.length > 0);
      people = people.filter(p => p !== person);

      this.$emit('updatePeople', people);
      this.$emit('updatePayments', payments);
    },
  }
}
</script>