<template>
  <div class="modal-overlay">
    <div class="modal">
      <button class="close-button" @click="this.$emit('closeModal');">×</button>
      <h2>결제 내역 {{ selectedPaymentIndex != null ? '수정' : '추가' }}</h2>
      <label for="description">설명</label>
      <input type="text" v-model="newPayment.description" id="description" />

      <label for="amount">금액</label>
      <input type="number" v-model="newPayment.amount" id="amount" />

      <label for="payer">결제자</label>
      <select v-model="newPayment.payer" id="payer">
        <option v-for="person in people" :key="person" :value="person">{{ person }}</option>
      </select>

      <label>참가자</label>
      <div class="participants-container">
        <div
            class="participant-item" @click="selectAll()"
            :class="{'selected': isAllSelected()}"
        >
          전체
        </div>
        <div
            class="participant-item"
            v-for="person in people"
            :key="person"
            @click="toggleParticipant(person)"
            :class="{'selected': isSelected(person) && !isAllSelected()}"
        >
          {{ person }}
        </div>
      </div>

      <div class="modal-actions" v-if="selectedPaymentIndex == null">
        <button class="add-button" @click="addPayment()" style="margin-left: auto">추가</button>
      </div>
      <div class="modal-actions" v-else>
        <button class="delete-button" @click="deletePayment()">삭제</button>
        <button class="add-button" @click="editPayment()">수정</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AddPaymentModal',
  props: {
    people: {
      required: false
    },
    payments: {
      required: false
    },
    selectedPaymentIndex: {
      required: false
    },
  },
  data() {
    return {
      newPayment: {
        description: '',
        amount: null,
        payer: '',
        participants: []
      },
    }
  },
  mounted() {
    if (this.people.length == 0) {
      alert("사용자를 추가해주세요.");
      this.$emit('closeModal');
    }

    if (this.selectedPaymentIndex != null) {
      this.newPayment = this.payments[this.selectedPaymentIndex];
    }
  },
  methods: {
    toggleParticipant(person) {
      if(this.isAllSelected()){
        this.newPayment.participants = [];
      }

      const index = this.newPayment.participants.indexOf(person);
      if (index === -1) {
        this.newPayment.participants.push(person);
      } else {
        this.newPayment.participants.splice(index, 1);
      }
    },
    selectAll(){
      if(this.isAllSelected()){
        this.newPayment.participants = [];
      }else{
        this.newPayment.participants = JSON.parse(JSON.stringify(this.people));
      }
    },
    isSelected(person) {
      return this.newPayment.participants.includes(person);
    },
    isAllSelected() {
      return this.newPayment.participants.length == this.people.length;
    },
    addPayment() {
      if(!this.validatePaymentInputs()){
        return;
      }

      let payments = this.payments;
      payments.push({
        description: this.newPayment.description,
        payer: this.newPayment.payer,
        participants: this.newPayment.participants,
        amount: this.newPayment.amount
      });

      this.$emit('updatePayments', payments);
      this.$emit('closeModal');
      this.resetNewPayment();
    },
    resetNewPayment() {
      this.newPayment = {
        description: '',
        amount: null,
        payer: '',
        participants: []
      };
    },
    validatePaymentInputs(){
      if (!this.newPayment.description){
        alert("설명을 입력하세요. ");
        return false;
      }else if (!this.newPayment.payer){
        alert("결제자를 선택하세요. ");
        return false;
      }else if (this.newPayment.participants.length == 0){
        alert("참가자를 선택하세요. ");
        return false;
      }else if (!this.newPayment.amount){
        alert("가격을 입력하세요. ");
        return false;
      }

      return true;
    },
    editPayment(){
      if(!this.validatePaymentInputs()){
        return;
      }

      let payments = this.payments;
      payments[this.selectedPaymentIndex] = this.newPayment;

      this.$emit('updatePayments', payments);
      this.$emit('resetSelectedPaymentIndex');
      this.$emit('closeModal');
      this.resetNewPayment();
    },
    deletePayment(){
      if(!confirm("삭제하시겠습니까?")){
        return;
      }

      let payments = this.payments;
      payments.splice(this.selectedPaymentIndex, 1);

      this.$emit('updatePayments', payments);
      this.$emit('resetSelectedPaymentIndex');
      this.$emit('closeModal');
    },
  }
}
</script>