<template>
  <main>
    <div class="container">
      <div class="people-section">
        <div class="title-container">
          <div class="title">사용자</div>
          <button class="add-button circle" @click="addPerson">+</button>
        </div>

        <PeopleList
            :people="people"
            :payments="payments"
            @updatePeople="updatePeople"
            @updatePayments="updatePayments"
        />
      </div>

      <div class="payment-section">
        <div class="title-container">
          <div class="title">결제내역</div>
          <button class="add-button circle" @click="openModal('addPayment')">+</button>
          <button class="reset-button" @click="resetPayments()" v-if="payments.length > 0">초기화</button>
        </div>

        <PaymentList
            :people="people"
            :payments="payments"
            @openEditPaymentModal="(index) => (selectedPaymentIndex = index, openModal('addPayment'))"
        />
      </div>

      <button class="calculate" @click="openModal('calculateResult')">계산하기</button>

      <!-- 결제 내역 추가/수정 모달 -->
      <AddPaymentModal
          v-if="showModal && modalType == 'addPayment'"
          :people="people"
          :payments="payments"
          :selectedPaymentIndex="selectedPaymentIndex"
          @closeModal="closeModal"
          @updatePayments="updatePayments"
          @resetSelectedPaymentIndex="selectedPaymentIndex = null"
      />

      <!-- 계산 결과 모달 -->
      <CalculateResultModal
          v-if="showModal && modalType == 'calculateResult'"
          :people="people"
          :payments="payments"
          @closeModal="closeModal"
      />
    </div>
  </main>
</template>

<script>

import styles from './assets/App.css';
import AddPaymentModal from './components/modal/AddPaymentModal.vue';
import CalculateResultModal from './components/modal/CalculateResultModal.vue';
import PaymentList from "@/components/PaymentList.vue";
import PeopleList from "@/components/PeopleList.vue";

export default {
  computed: {
    $style() {
      return styles;
    }
  },
  components: {
    PeopleList,
    PaymentList,
    AddPaymentModal,
    CalculateResultModal
  },
  data() {
    return {
      people: [],
      payments: [],
      showModal: false,
      modalType: '',
      selectedPaymentIndex: null
    };
  },
  mounted() {
    this.setPeopleAndPaymentsByLocalStorage();
  },
  methods: {
    addPerson() {
      const newName = prompt("추가할 이름을 입력하세요:");

      if(!newName){
        return;
      }

      if(this.people.includes(newName)){
        alert("이미 존재하는 사용자입니다. ");
        return;
      }

      // 기존에 참가자가 '전체'로 선택된 결제내역인 경우 '전체'로 유지되도록 세팅
      let beforePeopleCnt = this.people.length;
      let payments = this.payments;

      this.people.push(newName);
      payments.forEach(payment => {
        if (payment.participants.length === beforePeopleCnt) {
          payment.participants.push(newName)
        }
      })

      this.updatePayments(payments);
    },
    openModal(modalType) {
      this.modalType = modalType;
      this.showModal = true;
    },
    closeModal() {
      this.showModal = false;
    },
    resetPayments(){
      if(!confirm("초기화하시겠습니까?")){
        return;
      }

      this.updatePayments([]);
    },
    setLocalStorage(){
      window.localStorage.setItem('people', JSON.stringify(this.people));
      window.localStorage.setItem('payments', JSON.stringify(this.payments));
    },
    setPeopleAndPaymentsByLocalStorage(){
      let people = JSON.parse(window.localStorage.getItem('people'));
      let payments = JSON.parse(window.localStorage.getItem('payments'));

      // TODO LocalStorage 값 유효성 체크

      this.people = people;
      this.payments = payments;
    },
    updatePeople(people){
      this.people = people;
      this.setLocalStorage();
    },
    updatePayments(payments){
      this.payments = payments;
      this.setLocalStorage();
    }
  }
}
</script>